.main {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 20px;

  padding-top: 100px;
  padding-bottom: 50px;

  @include media-query(500px) {
    padding-top: 50px; }

  &__item {
    height: 150px;

    display: flex;
    align-items: center;
    justify-content: center;

    position: relative;

    color: $bg-color;
    border-radius: $border-radius;
    background-color: $primary-color;
    text-decoration: none;

    &:hover {
      cursor: pointer;
      animation: jelly 0.5s; }

    @keyframes jelly {
      0%,
      100% {
        transform: scale(1, 1); }
      25% {
        transform: scale(0.9, 1.1); }
      50% {
        transform: scale(1.1, 0.9); }
      75% {
        transform: scale(0.95, 1.05); } }

    &--add {
      background-color: $secondary-color;

      &::after,
      &::before {
        @include pseudo;

        width: 40px;
        height: 10px;

        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        background-color: $primary-color; }
      &::before {
        width: 10px;
        height: 40px; } } } }

.player {
  height: 150px;

  display: flex;
  flex-direction: column;
  align-items: center;

  position: relative;
  overflow: hidden;

  border-radius: $border-radius;
  background-color: $secondary-color;

  &:hover {
    cursor: pointer;
    animation: jelly 0.5s;

    .player__add,
    .player__subtract {
      transform: translateX(0); } }

  &__name {
    font-size: 24px;
    line-height: 29px;
    color: $text-color;
    text-transform: uppercase;

    margin-bottom: 17px; }
  &__score {
    font-size: 48px;
    line-height: 58px;
    color: $primary-color;
    text-transform: uppercase; }
  &__add,
  &__subtract {
    width: 50%;
    height: 100%;

    position: absolute;
    top: 0;

    transition: $transition;
    background-color: $primary-color;

    &:hover {
      &::after,
      &::before {
        transform: translate(-50%, -50%) scale(1.25); } }
    &:active {
      &::after,
      &::before {
        transform: translate(-50%, -50%) scale(0.9); } } }


  &__add {
    right: 0;
    transform: translateX(101%);

    &::after,
    &::before {
      @include pseudo;

      width: 40px;
      height: 10px;

      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: $transition;

      background-color: $secondary-color; }
    &::before {
      width: 10px;
      height: 40px; } }
  &__subtract {
    left: 0;
    background-color: $secondary-color;
    transform: translateX(-101%);

    &::before {
      @include pseudo;

      width: 40px;
      height: 10px;

      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: $transition / 2;

      background-color: $primary-color; } } }



