@mixin media-query($max) {
  @media screen and (max-width: $max) {
    @content; } }

@mixin adaptive-font($fsMax, $fsMin, $lhMax, $lhMin, $maxWidth) {
  $addSize: $fsMax - $fsMin;
  $addHeight: $lhMax - $lhMin;
  $maxW: $maxWidth - 320;

  font-size: calc(#{$fsMin + px} + #{$addSize} * ((100vw - 320px) / #{$maxW}));
  line-height: calc(#{$lhMin + px} + #{$addHeight} * ((100vw - 320px) / #{$maxW})); }

@mixin pseudo($content: '', $display: block, $pos: absolute) {
	content: $content;
	display: $display;
	position: $pos; }
